@font-face {
  font-family: "Gotham-Black";
  src: url("./assets/fonts/Gotham-Font/Gotham-Black.otf") format("opentype");
  font-display: swap;
}
@font-face {
  font-family: "Gotham-Bold";
  src: url("./assets/fonts/Gotham-Font/Gotham-Bold.otf") format("opentype");
  font-display: swap;
}
@font-face {
  font-family: "Gotham-Light";
  src: url("./assets/fonts/Gotham-Font/Gotham-Light.otf") format("opentype");
  font-display: swap;
}
@font-face {
  font-family: "Gotham-Medium";
  src: url("./assets/fonts/Gotham-Font/GothamMedium_1.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Gotham-Thin";
  src: url("./assets/fonts/Gotham-Font/Gotham-Thin.otf") format("opentype");
  font-display: swap;
}
body {
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.VisibleChebox {
  font-size: 1rem;
  font-family: "Gotham-Light";
  display: grid;
  place-items: center;
  cursor: pointer;
 
}
.VisibleChebox > label {
  cursor: pointer;
  display: grid;
  place-items: center;
  width: 100%;
  height: 100%;
}
.hiddenChebox {
  display: none;
}

.hiddenChebox:checked ~ .VisibleChebox {
  font-family: "Gotham-Black";
}
